@charset "utf-8";
/* 重置样式 */
body,div, button, dd, dl, dt, fieldset, form, h1, h2, h3, h4, h5, h6, hr, input, legend, li, ol, ul, p, pre, td, textarea, th,a,span {
    margin:0;
    padding:0;
}
body{
    font-family: '微软雅黑';
}
h1,h2,h3,h4,h5,h6{
    font-size:16px;
    font-weight: normal;
}
b,strong{
    font-weight: normal;
}
em,i{
    font-style: normal;
}
a,u{
    text-decoration: none;
}
ul,ol,li{
    list-style: none;
}
img{
    border:0;
    // display:block;
    /* 解决IE低版本：当图片有超链接的时候，存在一个蓝色边框 */
}
input{
    /*清除input 聚焦时候的蓝色边框*/
    outline: none;
}
/* 清除浮动*/
.clear_fix::after{
	content:" ";display:block;height:0;overflow:hidden;
clear:both;visibility:hidden;
}
.clear_fix{ zoom:1}
